import React, { useState, useEffect, Fragment, lazy, Suspense } from "react";
import { Responsive, Visibility } from "semantic-ui-react";
import { getNearByProblems } from "../../../ApiRequests/request";
import { connect } from "react-redux";
import { SpeedDial, SpeedDialAction } from "@material-ui/lab";
import {
  ThemeProvider,
  Menu,
  MenuItem,
  createMuiTheme,
  Button,
  Checkbox,
  Zoom,
  Snackbar,
  IconButton,
  Slider,
} from "@material-ui/core";
import { orange, blue, red } from "@material-ui/core/colors";
import {
  Filter,
  Sort,
  ListAlt,
  Close,
  RotateRightOutlined,
  MergeTypeOutlined,
} from "@material-ui/icons";
import { T } from "../../Fallback/T";
const ForComp = lazy(() => import("./forComp/forComp"));
const ForTab = lazy(() => import("./forTablet/forTab"));
const ForMob = lazy(() => import("./forMobile/forMob"));
const theme = createMuiTheme({
  palette: {
    primary: orange,
    secondary: {
      main: "rgba(255,255,255,0.5)",
    },
  },
});

const filterMenu = [
  { type: "none", name: "Show All" },
  // { type: "upperAuth", name: "Visible To Faculty ?" },
  // { type: "nonUpperAuth", name: "Not Visible To Faculty" },
  { type: "public", name: "Public" },
  { type: "anonymous", name: "Anonymous" },
];
const sortMenu = [
  { type: "latest", name: "Latest" },
  { type: "oldest", name: "Oldest" },
  // { type: "upVotes", name: "By UpVotes" },
  // { type: "downVotes", name: "By DownVotes" },
];
const typeMenu = [
  { type: "all", name: "All" },
  { type: "Technical", name: "Technical" },
  { type: "Academic", name: "Academic" },
  { type: "Registration", name: "Registration" },
  { type: "TP", name: "Placement" },
  { type: "Others", name: "Others" },
];
const Problems = (props) => {
  const [isError, setError] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [pageNo, setPageNo] = useState(1);
  const [Problems, setProblems] = useState([]);
  const [requestMade, setRequestMade] = useState(0);
  const [totalProblems, setTotal] = useState(0);
  const [whichState, setState] = useState(null);
  const [newComp, setNewComp] = useState(false);
  const getProblems = async (pageNo, options) => {
    try {
      setLoading(true);
      const response = await getNearByProblems(
        props.token,
        (pageNo - 1) * 10,
        options
      );
      if (response.ok) {
        const { problems, count } = await response.json();
        if (problems) {
          setProblems(problems);
          setTotal(count);
          setLoading(false);
        }
        return;
      }
      setLoading(false);
      setError(true);
    } catch (e) {
      setLoading(false);
      setError(true);
    }
  };
  const [elAnchor, setElAnchor] = useState(null);
  const [isSpeedDialOpen, setSpeedDial] = useState(false);
  const [scrollDirection, setScrollDirection] = useState("up");
  const [filter, setFilter] = useState({ current: "none", prev: "none" });
  const [sortBy, setSortBy] = useState({ current: "latest", prev: "latest" });
  const [Type, setType] = useState({ current: "all", prev: "all" });
  const [MinUpVotes, setMU] = useState(0);
  useEffect(() => {
    if (props.socket && !props.socket._callbacks.$complaint) {
      props.socket.on("complaint", (data) => {
        if (data.action === "added") {
          setNewComp(true);
        }
      });
    }

    if (requestMade === 0) {
      getProblems(pageNo, {
        sortBy: sortBy.current,
        filter: filter.current,
        Type: Type.current,
      });
      setRequestMade(1);
    }

    if (
      filter.current !== filter.prev ||
      sortBy.current !== sortBy.prev ||
      Type.current !== Type.prev
    ) {
      getProblems(1, {
        sortBy: sortBy.current,
        filter: filter.current,
        Type: Type.current,
      });
      setPageNo(1);
      setFilter({ ...filter, prev: filter.current });
      setSortBy({ ...sortBy, prev: sortBy.current });
      setType({ ...Type, prev: Type.current });
    }
  });

  return (
    <div>
      <Snackbar
        open={newComp}
        autoHideDuration={6000}
        onClose={() => {
          setNewComp(false);
        }}
        ContentProps={{
          style: { fontFamily: "poppins", fontSize: 16 },
        }}
        transitionDuration={300}
        message="There maybe new complaints"
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
        action={
          <Fragment>
            <IconButton
              style={{ color: "white" }}
              onClick={() => {
                getProblems(1, { sortBy: "latest", filter: "none" });
                setFilter({ ...filter, prev: filter.current });
                setSortBy({ ...sortBy, prev: sortBy.current });
                setNewComp(false);
              }}
            >
              <RotateRightOutlined></RotateRightOutlined>
            </IconButton>
            <IconButton
              onClick={() => {
                setNewComp(false);
              }}
              style={{ color: red[600] }}
            >
              <Close></Close>
            </IconButton>
          </Fragment>
        }
      ></Snackbar>

      <ThemeProvider theme={theme}>
        <Visibility
          onUpdate={(e, visibility) => {
            setScrollDirection(visibility.calculations.direction);
          }}
        >
          <Menu
            anchorEl={elAnchor}
            open={Boolean(elAnchor)}
            onClose={() => {
              setElAnchor(null);
              setState(null);
            }}
          >
            {whichState === "filter" &&
              filterMenu.map((item) => {
                return (
                  <MenuItem
                    key={item.name}
                    style={{ width: 200 }}
                    onClick={() => {
                      setFilter({ ...filter, current: item.type });
                      setElAnchor(null);
                      setSpeedDial(false);
                    }}
                  >
                    <Checkbox
                      checked={filter.current === item.type}
                      color="primary"
                    ></Checkbox>
                    <div
                      style={{
                        fontFamily: "poppins",
                        fontWeight: "bold",
                        fontSize: 13,
                      }}
                    >
                      {item.name}
                    </div>
                  </MenuItem>
                );
              })}
            {whichState === "sort" &&
              sortMenu.map((item) => {
                return (
                  <MenuItem
                    style={{ width: 200 }}
                    key={item.name}
                    onClick={() => {
                      setSortBy({ ...sortBy, current: item.type });
                      setElAnchor(null);
                      setSpeedDial(false);
                    }}
                  >
                    <Checkbox
                      color="primary"
                      name={item.type}
                      checked={sortBy.current === item.type}
                    ></Checkbox>
                    <div
                      style={{
                        fontFamily: "poppins",
                        fontWeight: "bold",
                        fontSize: 13,
                      }}
                    >
                      {item.name}
                    </div>
                  </MenuItem>
                );
              })}
            {whichState === "Type" &&
              typeMenu.map((item) => {
                return (
                  <MenuItem
                    style={{ width: 200 }}
                    key={item.name}
                    onClick={() => {
                      setType({ ...Type, current: item.type });
                      setElAnchor(null);
                      setSpeedDial(false);
                    }}
                  >
                    <Checkbox
                      color="primary"
                      name={item.type}
                      checked={Type.current === item.type}
                    ></Checkbox>
                    <div
                      style={{
                        fontFamily: "poppins",
                        fontWeight: "bold",
                        fontSize: 13,
                      }}
                    >
                      {item.name}
                    </div>
                  </MenuItem>
                );
              })}
          </Menu>

          <div
            style={{
              position: "fixed",
              right: "2%",
              bottom: "10%",
              zIndex: 100,
            }}
          >
            <SpeedDial
              ariaLabel="filter and sorter"
              icon={<ListAlt style={{ color: "white" }}></ListAlt>}
              open={isSpeedDialOpen}
              color="secondary"
              direction={"up"}
              hidden={scrollDirection !== "up" && !isSpeedDialOpen}
              onClose={() => {
                setSpeedDial(false);
              }}
              onOpen={() => {
                setSpeedDial(true);
              }}
            >
              <SpeedDialAction
                icon={<Filter></Filter>}
                tooltipTitle="Filters"
                tooltipOpen
                onClick={(e) => {
                  setState("filter");
                  setElAnchor(e.currentTarget);
                }}
              ></SpeedDialAction>
              <SpeedDialAction
                onClick={(e) => {
                  setState("sort");
                  setElAnchor(e.currentTarget);
                }}
                tooltipOpen
                tooltipTitle="SortBy"
                icon={<Sort></Sort>}
              ></SpeedDialAction>
              <SpeedDialAction
                onClick={(e) => {
                  setState("Type");
                  setElAnchor(e.currentTarget);
                }}
                tooltipOpen
                tooltipTitle="ComplaintType"
                icon={<MergeTypeOutlined></MergeTypeOutlined>}
              ></SpeedDialAction>
            </SpeedDial>
          </div>
          <Responsive {...Responsive.onlyComputer}>
            <Suspense fallback={T}>
              <ForComp
                deviceType={"comp"}
                Problems={Problems}
                id={props.id}
                pageNo={pageNo}
                setPageNo={(pageNo) => {
                  getProblems(pageNo, {
                    sortBy: sortBy.current,
                    filter: filter.current,
                    type: Type.current,
                  });
                  setPageNo(pageNo);
                }}
                problemsCount={totalProblems}
                isError={isError}
                isLoading={isLoading}
                {...props}
              ></ForComp>
            </Suspense>
          </Responsive>
          <Responsive {...Responsive.onlyTablet}>
            <Suspense fallback={T}>
              <ForTab
                {...props}
                deviceType={"tab"}
                Problems={Problems}
                id={props.id}
                pageNo={pageNo}
                setPageNo={(pageNo) => {
                  getProblems(pageNo, {
                    sortBy: sortBy.current,
                    filter: filter.current,
                    type: Type.current,
                  });
                  setPageNo(pageNo);
                }}
                problemsCount={totalProblems}
                isError={isError}
                isLoading={isLoading}
              ></ForTab>
            </Suspense>
          </Responsive>
          <Responsive {...Responsive.onlyMobile}>
            <Suspense fallback={T}>
              <ForMob
                {...props}
                deviceType={"mob"}
                Problems={Problems}
                id={props.id}
                pageNo={pageNo}
                setPageNo={(pageNo) => {
                  getProblems(pageNo, {
                    sortBy: sortBy.current,
                    filter: filter.current,
                    type: Type.current,
                  });
                  setPageNo(pageNo);
                }}
                problemsCount={totalProblems}
                isError={isError}
                isLoading={isLoading}
              ></ForMob>
            </Suspense>
          </Responsive>
        </Visibility>
      </ThemeProvider>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.userReducer.token,
    id: state.userReducer.id,
  };
};

export default connect(mapStateToProps)(Problems);
